.notifications {
  position: absolute;
  z-index: 3;
  bottom: 0;
  left: 0;
  right: 0; }
  @media (min-width: 640px) {
    .notifications {
      bottom: 30px;
      left: 30px;
      right: auto; } }

.notification {
  color: #fff;
  background: #323232;
  padding: 14px 24px;
  padding-right: 0;
  opacity: 0;
  display: flex; }
  @media (min-width: 640px) {
    .notification {
      min-width: 288px;
      max-width: 568px;
      border-radius: 2px; } }
  .notification .notification-body {
    margin-right: 48px;
    flex: 1; }
  .notification button {
    margin: 0;
    margin-right: 24px;
    color: red;
    text-transform: uppercase;
    min-width: min-content; }
    .notification button:focus {
      outline: none;
      color: #fff; }

.layout {
  display: flex;
  flex-flow: column;
  height: 100%; }

.main {
  flex: 1;
  display: flex;
  overflow: auto;
  position: relative; }

html, body {
  height: 100%;
  overflow: hidden;
  line-height: 0 !important; }

.scroller {
  overflow: hidden;
  overflow-y: scroll;
  width: 100%; }

.route-li {
  list-style: none;
  background: #d1703c;
  color: #fff;
  line-height: 2em;
  margin-bottom: 10px;
  padding: 0 0.5em;
  overflow: hidden;
  width: 25em; }

.stations-span {
  border: 0;
  display: table;
  margin: auto; }

#station-list {
  padding: 0 !important;
  text-align: center; }

.station-spacer {
  margin-right: 5px; }

.swing {
  perspective: 100px; }
  .swing .route-li {
    opacity: 0;
    transform: rotateX(-90deg);
    transition: all 0.5s cubic-bezier(0.36, -0.64, 0.34, 1.76); }
  .swing .route-table {
    opacity: 0;
    transform: rotateX(1deg);
    transition: all 0.5s cubic-bezier(0.36, -0.64, 0.34, 1.76); }
  .swing .station-show {
    opacity: 1;
    transform: none;
    transition: all 0.5s cubic-bezier(0.36, -0.64, 0.34, 1.76); }

.toolbar {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
  background: #3a5fff;
  color: #FFFFFF;
  height: 56px;
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.4); }
  @media (min-width: 600px) {
    .toolbar {
      height: 72px; } }
  .toolbar .site-title {
    font-size: 1.2rem;
    font-weight: normal;
    margin: 0;
    padding: 0;
    margin-left: 16px; }
    @media (min-width: 600px) {
      .toolbar .site-title {
        margin-left: 24px;
        font-size: 1.3rem; } }

#search_form {
  margin-top: 20px; }

legend {
  line-height: 1 !important;
  padding-bottom: 10px !important; }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v15/2UX7WLTfW3W8TclTUvlFyQ.woff") format("woff"); }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v15/d-6IYplOFocCacKzxwXSOD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff"); }

html {
  background: #fafafa; }

html, body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: transparent; }

pre,
code,
kbd,
samp,
tt {
  font-family: monospace;
  font-size: 1em; }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.unbutton {
  display: inline-block;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  white-space: nowrap; }
